import { TournamentStatus } from '../interfaces/tournament';

export const getTournamentStatus = (status: TournamentStatus) => {
  switch (status) {
    case 'scheduled':
      return '';
    case 'live':
      return ' ';
    case 'finished':
      return '';
    default:
      return '';
  }
};

export const getTournamentType = (type: string) => {
  switch (type) {
    case 'club':
      return '';
    case 'national':
      return '';
    default:
      return '';
  }
};
